  .picBorder{
    border-radius: 6px;
    border-style: solid;
    border-width: 1vh;
    border-color:#ffe593aa ;
  }
  .textRight{
    text-align: right;
  }
  .textLeft{
    text-align: left;
  }
  .picWord{
    font-size:4vmin;
    font-weight: 900;
    color:white;
    position: absolute;
    text-shadow: black 0.1em 0.1em 0.2em;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  
  }
  .focusWord{
    color:#ffe593aa;
    text-shadow: black 0.1em 0.1em 0.2em;
    font-size: 6vmin;
    font-weight: 500;
  }
  .highlight-half{
    background: linear-gradient(to top, #c0c0c0aa 35%, transparent 35%);
  }
  .highlight{
    padding: 0 2px;
    white-space: pre-line;
    border-radius: 2px;
    background-color: #c0c0c0aa;
  }